import "whatwg-fetch";

const fetchJSON = (url = ``) => {
    return fetch(url)
        .then(res => res.json())
        .then(
            (json) => {
                return json;
            }
        )
        .catch(err => console.log(`fetch error`, err));
};

// async / await
async function getDatas(url = ``) {
    try {
        return await fetchJSON(url);
    } catch (err) {
        console.error("getDatas error:\n", err);
    }
}


export default fetchJSON;
export {
    fetchJSON,
    getDatas
};



