// css
import "../css/latest-news.css";
// utils
import { FetchIP } from "../utils/urls";
import { fetchJSON, getDatas } from "../utils/fetch-ajax";
import { $qs, $qsa } from "../utils/no-need-jquery";
import { settingsGenerator } from "./settings";

import { showContent } from "../000-old/framework-show-content";

const fetchIP = FetchIP;


// ./latest-news.json

// fetch data
const url = `./latest-news.json`;

const showDOM = (url = ``) => {
    fetchJSON(url)
    .then(
        (json) => {
            // console.log(`json =`, json);
            // do something...
            // let datas = json.datas;
            // let keys = Object.keys(json.datas);
            let values = Object.values(json.datas);
            let tds = [...$qsa(`[data-value="latest-news-td"]`)];
            tds.forEach(
                (td, i) => {
                    td.innerHTML = "";
                    // td.insertAdjacentHTML(`beforeend`, datas[keys[i]]);
                    td.insertAdjacentHTML(`beforeend`, values[i]);
                }
            );
        }
    )
    .catch((err) => {throw new Error(`fetch data error`, err);});
};


if (document.createElement("template").content) {
    console.log("YES! The browser supports the template element");
    // templateGenerator(arr);
    setTimeout(() => {
        // if (container) {
        //     showContent(uid, container);
        // } else {
        //     showContent(uid);
        // }
        let uid = `latest-news`;
        showContent(uid);
        // showDOM
        showDOM(url);
        // customise settings
        settingsGenerator(`.f10_latest-news_title-box`);
        let delete_uid = $qs(`[data-module-box="delete"]`);
        delete_uid.addEventListener(`click`, (e) => {
            // delete_uid
            console.log(`dataset =`, e.target);
            // console.log(`dataset =`, e.target.dataset);
            // console.log(`parentNode dataset =`, e.target.parentNode.dataset.deleteUid);
            // console.log(`parentElement dataset =`, e.target.parentElement.dataset.deleteUid);
            // console.log(`delete uid =`, e.target.dataset.deleteUid);
            // console.log(`this delete uid =`, this.dataset.deleteUid);
            // console.log(`delete_uid uid =`, delete_uid.dataset.deleteUid);
            let uid = e.target.parentElement.dataset.deleteUid,
                box = $qs(`[data-modules-container="${uid}"]`);
            // box = document.querySelector(`[data-modules-container="latest-news"]`);
            // box
        });
    }, 0);
} else {
    console.error("No! The browser does not support the template element");
}


