import {
    $qs
} from "../utils/no-need-jquery";

// template to html
const FrameworkShowContent = (uid = ``, container = `[data-html="html-container"]`, debug = false) => {
    if (uid) {
        let temp = $qs(`[data-template="template-${uid}"]`),
            clone = temp.content.cloneNode(true);
        // container
        let box = $qs(container);
        box.appendChild(clone);
    } else {
        console.error(`uid can not be find!`, uid);
        // console.warn(`uid can not be find!`, uid);
        // console.log(`%c uid can not be find!`, `color: red; font-size: 16px;`, uid);
    }
};



export default FrameworkShowContent;

export {
    FrameworkShowContent,
    FrameworkShowContent as showContent
};



