import { $qs, $qsa } from "../utils/no-need-jquery";

const settingsGenerator = (uid = ``, debug = false) => {
    if (uid) {
        let template = $qs(uid);
        // let div = document.createElement(`div`);
        // template.insertAdjacentElement(`afterbegin`, div);
        let config = `
            <div data-module-box="delete" data-delete-uid="latest-news">
                <span data-delete-span="delete-span" title="你确定要删除此模块?" data-delete-uid="latest-news">
                    删除
                </span>
            </div>
        `;
        // template
        template.insertAdjacentHTML(`beforeend`, config);
        // template.insertAdjacentHTML(`afterbegin`, config);
    } else {
        console.error(`setttings "uid" is missing!`, uid);
    }
};

export default settingsGenerator;

export {
    settingsGenerator
};





